"use client";
import { useState } from "react";
import Link from "next/link";
import { FaBars, FaTimes } from "react-icons/fa";

export default function Navbar() {
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <nav className="bg-[#171717] text-white p-4">
      <div className="container mx-auto flex justify-between items-center">
        {/* LOGO */}
        <Link href="/" className="text-2xl font-bold">
          MeuSite
        </Link>

        {/* MENU DESKTOP */}
        <ul className="hidden md:flex space-x-6">
          <li>
            <Link href="/" className="hover:text-gray-400">Início</Link>
          </li>
          <li>
            <Link href="/sobre" className="hover:text-gray-400">Sobre</Link>
          </li>
          <li>
            <Link href="/contato" className="hover:text-gray-400">Contato</Link>
          </li>
        </ul>

        {/* BOTÃO DO MENU MOBILE */}
        <button
          onClick={() => setMenuOpen(!menuOpen)}
          className="md:hidden text-2xl"
        >
          {menuOpen ? <FaTimes /> : <FaBars />}
        </button>
      </div>

      {/* MENU MOBILE */}
      {menuOpen && (
        <ul className="md:hidden bg-[#222] p-4 absolute top-16 left-0 w-full space-y-4 text-center">
          <li>
            <Link href="/" className="block py-2" onClick={() => setMenuOpen(false)}>Início</Link>
          </li>
          <li>
            <Link href="/sobre" className="block py-2" onClick={() => setMenuOpen(false)}>Sobre</Link>
          </li>
          <li>
            <Link href="/contato" className="block py-2" onClick={() => setMenuOpen(false)}>Contato</Link>
          </li>
        </ul>
      )}
    </nav>
  );
}
